import React from 'react'
import { graphql } from 'gatsby'
import { Helmet } from 'react-helmet'
import { Jumbotron } from 'reactstrap'

import BreadcrumbNav from '../components/breadcrumb-nav'
import Layout from '../components/layout'
import Toc from '../components/toc'
import {
  flattenFileNode,
  matterNameFromMatter,
  classeNameFromClasse,
  classeWikidataURIFromClasse,
} from '../helpers'

import '../scss/cours.scss'
import 'katex/dist/katex.min.css'

export default ({ data, location }) => {
  const course = flattenFileNode(data.markdownRemark)

  const title = `${course.chapter} un cours de ${matterNameFromMatter(
    course.matter
  )} en ${classeNameFromClasse(
    course.classe
  )} proposé par lyceum un site open-source pour le lycée`

  const description = `Cours de ${classeNameFromClasse(
    course.classe
  )} en ${matterNameFromMatter(course.matter)} intitulé: «${course.chapter}»`

  return (
    <Layout location={location} data={data}>
      <Helmet
        title={`${title}`}
        meta={[
          {
            name: `description`,
            description: `${description}`,
          },
          { name: `keywords`, content: course.tags.join(`, `) },
        ]}
      >
        <script type="application/ld+json">{`
         {"@context": "http://schema.org/",
          "@type": "Course",
          "name": "${course.part} | ${course.chapter}",
          "description": "${description}",
          "learningResourceType": [
            "lesson"    ],
          "provider": {
            "@type": "schema:EducationalOrganization",
            "name": "lyceum.fr"
          },
          "audience": {
            "@type": "EducationalAudience",
            "educationalRole": "student"
          },
          "educationalAlignment": {
            "@type": "AlignmentObject",
            "alignmentType": "educationalLevel",
            "educationalFramework": "French Grade Levels",
            "targetName": "${classeNameFromClasse(course.classe)}",
            "@id": "${classeWikidataURIFromClasse(course.classe)}"
          },
          "url": "${location.href}"
        }
    `}</script>
      </Helmet>
      <BreadcrumbNav node={course} />
      <Jumbotron>
        <h1 className="display-1">{`Chapitre ${course.nChapter}: ${course.chapter}`}</h1>
        <Toc tableOfContents={course.tableOfContents} />
      </Jumbotron>
      <div dangerouslySetInnerHTML={{ __html: course.html }} />
    </Layout>
  )
}

export const query = graphql`
  query MDCourseQuery($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      tableOfContents(maxDepth: 3)
      frontmatter {
        tags
      }
      fields {
        classe
        chapter
        matter
        nChapter
        part
        nPart
      }
    }
  }
`
